:root {
  --main-text-color: #000;
  --second-text-color: #8c8c8c;
  --dark-text-color: #545454;
  --white-text-color: #fff;
  --header-bg-color: rgba(0, 0, 0, .56);
  --accent-color: #ff6b08;
  --hover-animation: 300ms cubic-bezier(.38, .53, .8, .71);
  --second-white-color: #f7f7f7;
  --second-dark-bg-color: #1a1818;
  --main-dark-bg-color: #383838;
}

html {
  box-sizing: border-box;
  scroll-behavior: smooth;
}

*, :before, :after {
  box-sizing: inherit;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

a {
  color: inherit;
  text-decoration: none;
  display: block;
}

h1, h2, h3, h4, h5, h6, p {
  margin: 0;
  padding: 0;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

body {
  color: var(--main-text-color);
  background-color: var(--main-bg-color);
  font-family: Roboto, sans-serif;
  overflow-x: hidden;
}

.container {
  width: 100%;
